@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap');

html, body {
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
  transition: background-color 0.3s, color 0.3s;
}
* {
  box-sizing: border-box;
}
:root {
  --color-bg: #121212;
  --color-text: #fff;
  --color-card-bg: rgba(255,255,255,0.1);
}
[data-theme="light"] {
  --color-bg: #fff;
  --color-text: #000;
  --color-card-bg: rgba(0,0,0,0.05);
}
body {
  background-color: var(--color-bg);
  color: var(--color-text);
}
a {
  text-decoration: none;
  color: var(--color-text);
}
a:hover {
  opacity: 0.8;
}
.glass {
  backdrop-filter: blur(10px);
  background-color: var(--color-card-bg);
  border-radius: 12px;
  transition: box-shadow 0.2s, transform 0.2s;
}
.glass:hover {
  box-shadow: 0 0 12px rgba(255,255,255,0.15);
  transform: translateY(-2px);
}
.glass:active {
  transform: translateY(0);
  box-shadow: 0 0 6px rgba(255,255,255,0.2);
}
.cards-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px,1fr));
  gap: 20px;
  align-items: start;
  margin-top: 20px;
}
/* Общие стили кнопок */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 9999px;
  padding: 14px 24px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: opacity 0.2s, box-shadow 0.2s, transform 0.2s;
  text-decoration: none;
  border: 2px solid transparent;
}
.btn:hover {
  opacity: 0.9;
  box-shadow: 0 0 8px rgba(255,255,255,0.3);
  transform: scale(1.02);
}
.btn:active {
  transform: scale(0.98);
}
/* Кнопки зависящие от темы (для шапки, «Подробнее», плееры, пагинация и т.д.) */
[data-theme="dark"] .btn-site {
  background-color: #222;
  color: #fff;
  border: 2px solid #333;
}
[data-theme="light"] .btn-site {
  background-color: #f0f0f0;
  color: #000;
  border: 2px solid #000;
}
/* Кнопка выбранная (напр. текущая страница, выбранный плеер) */
[data-theme="dark"] .btn-site.selected {
  background-color: #444;
  border-color: #444;
}
[data-theme="light"] .btn-site.selected {
  background-color: #ccc;
  border-color: #ccc;
}
/* Иконки для темы (солнце/луна) */
.icon {
  width: 24px;
  height: 24px;
  transition: transform 0.2s ease;
}
.icon.rotate {
  transform: rotate(180deg);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in {
  animation: fadeIn 0.5s ease-in-out;
}
